<template>
    <div class="order">
        <van-nav-bar
            title="订单"
            left-arrow
            @click-left="cartvue"
        />
        <div @click="addressfun">
            <van-contact-card 
            :type="cardType" 
            :name="name" 
            :tel="tel" 
            add-text="添加联系人"
            :editable="false" />
        </div>
        <div v-for="(item,index) in lists" :key="index">
            <van-card
            :num="item.num"
            :price="item.price"
            :title="item.name"
            :thumb="item.image"
            />
        </div>

        <van-submit-bar 
        :price="total*100" 
        button-text="提交订单" @submit="onsubmit" />
    </div>
</template>

<script>
    import "../assets/css/order.less";
    export default {
        data(){
            return{
                name:'',
                tel:''
            }
        },
        methods:{
            sum(){
                this.total = this.lists.reduce((total,item)=>{
                    if(!item.isSelect) return total; 
                    return total + parseInt(item.price) * parseInt(item.num); 
                },0) 
            },
            addressfun(){
                if(this.$store.state.address.lists.length==0){
                    this.$router.push('/addressAdd')
                }else{
                    this.$router.push('/address')
                }
            },
            cartvue(){
                let order = {
                    orderId:new Date().getTime(),
                    address:this.address,
                    goodsList:this.lists,
                    dateTime:new Date(),
                    type:1 
                }
                this.$store.commit("order/add",order);
                this.$store.state.cart.lists = [];
                this.$store.state.cart.num = 0;
                this.$router.push('/main/shopcart');
            },
            onsubmit(){
                let order = {
                    orderId:new Date().getTime(),
                    address:this.address,
                    goodsList:this.lists,
                    dateTime:new Date(),
                    type:2 
                }
                this.$store.commit("order/add",order);
                this.$store.state.cart.lists = [];
                this.$store.state.cart.num = 0;
                this.$router.push("/orderList/orderAll")
            }
        },
        computed:{
            cardType(){
                return this.$store.state.address.lists.length==0 ? "add":"edit"
            }
        },
        created(){
            let data = this.$store.state.address.lists.find(item=>item.isDefault);
            if(data){
                this.name = data.name;
                this.tel = data.tel;
                this.address = data; 
            }
            this.lists = this.$store.state.cart.lists;
            this.sum();
        },
    }
</script>

<style lang="less" scoped>

</style>